import React, {useState, useEffect} from "react";
import {connect} from "react-redux";
import {addReview, fetchFormData} from "../../store/actions/api-actions";
import {getFormData} from "../../store/reducers/selectors";

const FormDeclarer = (props) => {
  const {token, formData, history, onSubmit, loadCurrentForm} = props;

  const [answered, setAnswered] = useState();
  const [advice, setAdvice] = useState();
  const [rating, setRating] = useState();
  const [textComment, setText] = useState(``);

  console.log(formData);

  useEffect(() => {
    loadCurrentForm(token, formData);
  }, [token]);

  if (!formData) {
    return null;
  }

  return (
    <section className="wrapper">
      <header className="header">
        <picture className="header__logo">
          <img src="../img/Logo_DGI.png" alt="    "/>
        </picture>
      </header>
      <div className="wrapper__form">
        <h3> </h3>
        <text> {formData.datetime}  : {formData.theme}</text>
        <form
          action="#"
          className="add-review__form"
          onSubmit={(e) => {
            e.preventDefault();
            e.stopPropagation();
            onSubmit(token, answered, advice, rating, textComment);
          }
          }>
          <fieldset class="mb-5">
            <h5>      ?</h5>
            <div className="custom-control custom-radio custom-control-inline">
              <input type="radio"  value="Yes" id="answeredYes" name="answered" className="custom-control-input"
                onChange={(e) => {
                  setAnswered(e.target.value);
              }}/>
              <label className="custom-control-label" htmlFor="answeredYes"></label>
            </div>
            <div className="custom-control custom-radio custom-control-inline">
              <input type="radio" value="No" id="answeredNo" name="answered" className="custom-control-input"
                onChange={(e) => {
                  setAnswered(e.target.value);
                }}/>
              <label className="custom-control-label" htmlFor="answeredNo"></label>
            </div>
          </fieldset>
          <fieldset class="mb-5">
            <h5>    ?</h5>
            <div className="custom-control custom-radio custom-control-inline">
              <input type="radio" value="Yes" id="isAdviceYes" name="isAdvice" className="custom-control-input"
               onChange={(e) => {
                setAdvice(e.target.value);
              }}/>
              <label className="custom-control-label" htmlFor="isAdviceYes"></label>
            </div>
            <div className="custom-control custom-radio custom-control-inline">
              <input type="radio" value="No" id="isAdviceNo" name="isAdvice" className="custom-control-input"
               onChange={(e) => {
                setAdvice(e.target.value);
              }}/>
              <label className="custom-control-label" htmlFor="isAdviceNo"></label>
            </div>
          </fieldset>
          <div class="d-block mb-3">
            <h5>  </h5>
            <div className="custom-control custom-radio ">
              <input type="radio" value="" id="rating1" name="rating" className="custom-control-input"
               onChange={(e) => {
                setRating(e.target.value);
              }}/>
              <label className="custom-control-label" htmlFor="rating1"></label>
            </div>
            <div className="custom-control custom-radio ">
              <input type="radio" value="" id="rating2" name="rating" className="custom-control-input"
              onChange={(e) => {
                setRating(e.target.value);
              }}/>
              <label className="custom-control-label" htmlFor="rating2"></label>
            </div>
            <div className="custom-control custom-radio ">
              <input type="radio" value="" id="rating3" name="rating" className="custom-control-input"
              onChange={(e) => {
                setRating(e.target.value);
              }}/>
              <label className="custom-control-label" htmlFor="rating3"></label>
            </div>
            <div className="custom-control custom-radio ">
              <input type="radio" value=" " id="rating4" name="rating" className="custom-control-input"
              onChange={(e) => {
                setRating(e.target.value);
              }}/>
              <label className="custom-control-label" htmlFor="rating4"> </label>
            </div>
            <div className="custom-control custom-radio ">
              <input type="radio" value=" " id="rating5" name="rating" className="custom-control-input"
              onChange={(e) => {
                setRating(e.target.value);
              }}/>
              <label className="custom-control-label" htmlFor="rating5"> </label>
            </div>
          </div>


          <div className="add-review__text">
            <textarea
              className="add-review__textarea"
              name="textComment"
              id="text"
              placeholder="Review text"
              value={textComment}
              onChange={(e) => {
                setText(e.target.value);
                //checkValid(e);
              }}
            />
            <div className="add-review__submit">
              <button
                className="add-review__btn"
                type="submit"
                disabled={!true}
              >
                Post
              </button>
            </div>
          </div>
          {
            status &&
            <div className="add-review-error">
              {`Error adding comment: ${status.status} ${status.statusText}`}
            </div>
          }
        </form>
      </div>
    </section>
  )
};

const mapStateToProps = (state) => ({
  formData: getFormData(state),
})

const mapDispatchToProps = (dispatch) => ({

  loadCurrentForm(token) {
    dispatch(fetchFormData(token));
  },

  onSubmit(token, answered, advice, rating, textComment) {
    dispatch(addReview(token, answered, advice, rating, textComment));
  }
});

export {FormDeclarer};
export default connect(mapStateToProps, mapDispatchToProps)(FormDeclarer);
