import {extend} from "../../../utils/utils";
import {ActionType} from "../../actions/action";

const initialState = {
  formData: null
};

const data = (state = initialState, action) => {
  switch (action.type) {
    case ActionType.LOAD_FORM_DATA:
      return extend(state, {formData: action.payload});
  }

  return state;
};



export {data};
