
import React from "react";
import FormDeclarer from "../form-declarer/form-declarer";
import {Switch, Route, BrowserRouter} from "react-router-dom";


const App = () => {
  return (
    <BrowserRouter>
      <Switch>
          <Route
            exact path="/:token"
            render={({match, history}) => (
              <FormDeclarer
                token={match.params.token}
                history={history}
              />
            )}
          />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
